
@import 'colors';

/*------------BASIC FADE------------*/
.fadeTransition{
    &.ng-enter {
        transition: .3s;
        opacity: 0;
    }
    &.ng-enter-active {
        opacity: 1;
    }

    &.ng-leave {
        transition: .3s;  
        opacity: 1;
    }
 
    
    &.ng-leave-active {
        opacity: 0;
    }
}

.fadeLeaveTransition{


    &.ng-leave {
        transition: .3s;  
        opacity: 1;
    }
 
    
    &.ng-leave-active {
        opacity: 0;
    }
}

/*------------SLIDING DOWN FADE------------*/

.fadeSlideDownEnterTransition{
    &.ng-enter {
        transition: .3s;
        opacity: 0;
        transform: translate(0px, -500px);
    }
 
    &.ng-enter-active {
        opacity: 1;
        transform: translate(0px, 0px);
    }
}

.fadeSlideDownLeaveTransition{

    &.ng-leave {
        transition: .3s;
        transform: translate(0px, 0px);
        opacity: 1;
    }
 
  
    &.ng-leave-active {
      transform: translate(0px, -500px);
        opacity: 0;
    }
}


/*------------SLIDING UP FADE------------*/

.fadeSlideUpEnterTransition{
    &.ng-enter {
        transition: .3s;
        opacity: 0;
        transform: translate(0px, 500px);
    }
 
    &.ng-enter-active {
        opacity: 1;
        transform: translate(0px, 0px);
    }
}

.fadeSlideUpLeaveTransition{

    &.ng-leave {
        transition: .3s;
        transform: translate(0px, 0px);
        opacity: 1;
    }
 
  
    &.ng-leave-active {
      transform: translate(0px, 500px);
        opacity: 0;
    }
}



/*------------LEFT TO RIGHT FADE------------*/

.fadeSlideFromLeftEnterTransition{
    &.ng-enter {
        transition: .3s;
        opacity: 0;
        transform: translate(-1000px,0px);
    }
 
    &.ng-enter-active {
        opacity: 1;
        transform: translate(0px, 0px);
    }
}

.fadeSlideToRightLeaveTransition{

    &.ng-leave {
        transition: .3s;
        transform: translate(0px, 0px);
        opacity: 1;
    }
 
  
    &.ng-leave-active {
      transform: translate(1000px,0px);
        opacity: 0;
    }
}


