.thumbSlide{
   

    &.ng-enter {
      transition-delay: 2s;
        transition: .3s;
        // transform:  scale(0);
          height: 50px;
         width: 50px;
         overflow: hidden;
         text-overflow: clip;
    }
    &.ng-enter-active {
      transition-delay: 2s;
        opacity: 1;
        // transform:  scale(1);
        width: 230px;
    }

    &.ng-leave {
      transition-delay: 2s;
        transition: .3s;  
        opacity: 1;
    }
 
    
    &.ng-leave-active {
      transition-delay: 2s;
        opacity: 0;
    }
}

@keyframes mymove {
    0%   {top: 0px; background: red; width: 100px;}
    100% {top: 200px; background: yellow; width: 300px;}
}

.message{
  position: absolute;
  left: 50px;
  width: 200px;
  height: 50px;
  padding: 20px 10px;
}


.animate-enter { 
    -webkit-animation: enter 600ms cubic-bezier(0.390, 0.575, 0.565, 1.000);
    animation: enter 600ms cubic-bezier(0.390, 0.575, 0.565, 1.000);
    display: block;
    position: relative;
} 
@-webkit-keyframes enter {
    from {
        opacity: 0;
        height: 50px;
        width: 50px;
        overflow: hidden;
        text-overflow: clip;
        left: -100px;
        transition: .3s;
    }

    50% {
        left: 0px;
         width: 50px;
        overflow: hidden;
        text-overflow: clip;
    }

    90%{
      left: 0px;
       width: 50px;
        overflow: hidden;
        text-overflow: clip;
    }
    to {
        opacity: 1;
        height: 50px;
        left: 0px;
        top: 0px;
        width: 230px;
    }
}

/*------------GREEN LOADING SPINNER------------*/

// spinners

.bounceSpinnerContainer{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.bounceSpinner {
  width: 60px;
  height: 60px;

  position: relative;
  margin: 20px auto;
}


.double-bounce1, .double-bounce2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: $greenishTeal;
  opacity: 0.6;
  position: absolute;
  top: 0;
  left: 0;
  
  -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
  animation: sk-bounce 2.0s infinite ease-in-out;
}

.double-bounce2 {
  -webkit-animation-delay: -1.0s;
  animation-delay: -1.0s;
  background: $aquaGreen;
}

@-webkit-keyframes sk-bounce {
  0%, 100% { -webkit-transform: scale(0.0) }
  50% { -webkit-transform: scale(1.0) }
}

@keyframes sk-bounce {
  0%, 100% { 
    transform: scale(0.0);
    -webkit-transform: scale(0.0);
  } 50% { 
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
}



